<script>
export default {
  data() {
    return {
      vdata: {},
      formAction: "add",
      datanya: [],
      search: "",
      formModal: false,
    };
  },
  methods: {
    async resizeImageFile(inputFile, newWidth, newHeight) {
      return new Promise((resolve, reject) => {
        // Check if the file is an image
        if (!inputFile.type.startsWith("image/")) {
          reject(new Error("The input file is not an image."));
        }

        // Create a FileReader to read the image file
        const reader = new FileReader();

        // Set up the FileReader onload event to handle the image file
        reader.onload = async (event) => {
          const img = new Image();

          // Set up the Image onload event to handle the image data
          img.onload = () => {
            const canvas = document.createElement("canvas");
            canvas.width = newWidth;
            canvas.height = newHeight;

            const ctx = canvas.getContext("2d");
            ctx.drawImage(img, 0, 0, newWidth, newHeight);

            // Convert the canvas content to a new Blob
            canvas.toBlob(
              (blob) => {
                resolve(
                  new File([blob], inputFile.name, { type: inputFile.type })
                );
              },
              inputFile.type,
              1
            );
          };

          img.onerror = () => {
            reject(new Error("Failed to load the image file."));
          };

          img.src = event.target.result;
        };

        // Read the input file as a data URL
        reader.readAsDataURL(inputFile);
      });
    },
    async upload(e) {
      // const options = {
      //   maxSizeMB: 1,
      //   maxWidthOrHeight: 1920,
      //   useWebWorker: true,
      // }
      let that = this;
      // let file=e.target.files[0]
      var dimensions = await this.getDimensionsFromFile(e.target.files[0]);
      console.log("dimensions", dimensions);
      var config = {
        quality: 0.7,
        width: 1080,
        height: 1920,
        debug: true,
      };
      if (dimensions.height > dimensions.width) {
        this.vdata.dimension = "potrait";
        config = {
          quality: 0.7,
          width: 1080,
          height: 1920,
          debug: true,
        };
      } else {
        this.vdata.dimension = "landscape";
        config = {
          quality: 0.7,
          width: 1920,
          height: 1080,
          debug: true,
        };
      }
      let file = await this.resizeImageFile(e.target.files[0],config.width,config.height);
      // file = await imageCompression(file, options);
      var dimensions = await this.getDimensionsFromFile(file);
      console.log("dimensions", dimensions);
      this.vdata.height = dimensions.height;
      this.vdata.width = dimensions.width;
      console.log("file", file);
      const newName = this.vdata.nama_doc + e.target.files[0].name;
      const modifiedFile = new File([file], newName, { type: file.type });
      this.$forceUpdate();
      let fd = new FormData();
      fd.append("file", modifiedFile);
      axios.post("https://hit-ocr.online/api/upload/image", fd).then((res) => {
        that.vdata.filename = this.vdata.nama_doc + file.name;
        alert("Upload Berhasil...");
      });
    },
    getDimensionsFromFile(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = function (event) {
          const img = new Image();
          img.onload = function () {
            const width = img.width;
            const height = img.height;
            resolve({ width, height });
          };
          img.onerror = function () {
            reject("Error loading image.");
          };
          img.src = event.target.result;
        };
        reader.onerror = function () {
          reject("Error reading file.");
        };
        reader.readAsDataURL(file);
      });
    },
    getDimension() {
      if (this.vdata.dimension == "1") {
        this.vdata.height = 1920;
        this.vdata.width = 1080;
      } else if (this.vdata.dimension == "2") {
        this.vdata.height = 1080;
        this.vdata.width = 1920;
      }
    },
    async submitLayout() {
      if (this.formAction == "add") {
        const usersCollection = firebase.collection(
          firebase.firestore,
          "ocr-layouts"
        );
        this.formModal = false;
        //  this.getDimension()
        firebase
          .addDoc(usersCollection, this.vdata)
          .then((docRef) => {
            console.log("Layouts added with ID: ", docRef.id);
          })
          .catch((error) => {
            console.error("Error adding Layouts: ", error);
          });
      } else if (this.formAction == "update") {
        const docRef = firebase.doc(
          firebase.firestore,
          "ocr-layouts",
          this.vdata._id
        );
        // this.getDimension()
        delete this.vdata._id;
        firebase
          .updateDoc(docRef, this.vdata)
          .then(() => {
            console.log("Document updated successfully");
          })
          .catch((error) => {
            console.error("Error updating document:", error);
          });
      }
    },
    async removeData(item) {
      if (confirm("Are you sure to delete?")) {
        let doc = firebase.doc(firebase.firestore, "ocr-layouts", item._id);
        await firebase.deleteDoc(doc);
      }
    },
    async getData() {
      let that = this;
      const layouts = firebase.collection(firebase.firestore, "ocr-layouts");
      firebase.onSnapshot(
        layouts,
        (querySnapshot) => {
          that.datanya = [];
          querySnapshot.forEach((doc) => {
            that.datanya.push({ _id: doc.id, ...doc.data() });
          });
        },
        (error) => {
          console.error("Error getting real-time collection:", error);
        }
      );
    },
  },
  async mounted() {
    // const hasCameraPermission = await Camera.checkPermissions();
    // if (!hasCameraPermission.camera) {
    //   const permissionRequestResult = await Camera.requestPermissions();
    // }
    this.getData();
  },
};
</script>
